#content-detail {
  background: transparent;
  font-size: 100%;
  vertical-align: bottom; }
  #content-detail .header-center {
    height: 1px;
    background: #ddd;
    border-bottom: #fff solid 1px;
    margin-bottom: 30px;
    position: relative; }
    #content-detail .header-center h3 {
      background: whitesmoke;
      position: absolute;
      top: -10px;
      left: 50%;
      margin-left: -150px;
      width: 300px;
      text-align: center;
      display: block;
      color: #666666;
      text-transform: uppercase;
      font-size: 14px;
      font-weight: normal;
      letter-spacing: 1px;
      padding: 0px; }
  #content-detail .thumbs {
    list-style: none;
    position: relative;
    padding: 0; }
    #content-detail .thumbs:hover .clear {
      opacity: 0.2;
      -webkit-transition: all 0.5s ease-out;
      transition: all 0.5s ease-out; }
    #content-detail .thumbs .clear:hover {
      opacity: 1; }
    #content-detail .thumbs li {
      width: 30%;
      float: left;
      background: #fff;
      margin: 0 1.3% 2% 0;
      padding: 1%;
      border: 0;
      border-radius: 5px;
      box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2); }
      #content-detail .thumbs li a {
        text-decoration: none;
        display: block;
        position: relative;
        color: #333333; }
        #content-detail .thumbs li a img {
          max-width: 265px;
          position: relative;
          background-clip: padding-box;
          margin-left: 3px;
          border-radius: 3px; }
      #content-detail .thumbs li .description {
        position: relative;
        overflow: hidden; }
        #content-detail .thumbs li .description h4 {
          margin: 0;
          padding: 0;
          border: 0;
          font-size: 1.6rem;
          font-weight: 300;
          line-height: 1.2em; }
        #content-detail .thumbs li .description p {
          display: block;
          margin: 0;
          font-size: 1rem;
          color: #666666;
          line-height: 1.2em;
          padding-top: 0.3em;
          height: 1.3em;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis; }

.about-me {
  display: grid;
  grid-gap: 20px;
  grid-template-areas: "portrait-image  history history history" "dev1 dev2 dev3 dev4";
  grid-template-columns: repeat(4, 1fr); }
  .about-me .portrait-image {
    grid-area: portrait-image;
    margin: auto;
    border-radius: 50%;
    border: black 3px solid;
    background-size: contain;
    height: 180px;
    width: 180px; }
  .about-me .history {
    grid-area: history; }
    .about-me .history .about-info {
      width: 700px;
      font-size: 1.4rem;
      font-weight: 450;
      color: #c4c4c4; }
  .about-me .dev-1 {
    grid-area: dev1; }
  .about-me .dev-2 {
    grid-area: dev2; }
  .about-me .dev-3 {
    grid-area: dev3; }
  .about-me .dev-4 {
